.mainContentContainer{
  display: flex;
  row-gap: 1em;
  .cred-text-btn-container{
    width: 100%;
    /* row-gap: 0em; */
    column-gap: 1em;
    justify-content: center;
    align-items: center;
  }
  .add-btn{
    margin-bottom: 2em;
  }
    .table-container{
        width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    table {
      width: 100%;
      border-spacing: 0;
      border: 1px solid #f2efef;
    }
    th,
    td {
      border: 1px solid #f2efef;
      text-align: center;
      // padding: 1em;
    }
    th {
      background-color: #f4f0f0;
    }
    tbody {
      display: block;
      max-height: 10em;
      overflow: auto;
    }
    thead,
    tbody tr {
      display: table;
      width: 100%;
      table-layout: fixed;
    }
    td:last-child,
    th:last-child {
      width: 5em;
      overflow: hidden;
      white-space: nowrap;
    }
    }
}
.dialogActionBtn{
  display: flex;
  column-gap: 1em;
  .lds-ring {
    display: inline-block;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    // margin: 0.5em;
    margin: 5px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    // width: 64px;
    // height: 64px;
    // margin: 8px;
    // height: 1em;
    // width: 1em;
    height: 100%;
    width: 100%;
    // border: 8px solid #fff;
    border: 3px solid black;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    // border-color: #fff transparent transparent transparent;
    border-color: black transparent transparent transparent;
  }
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.attach-popup-actions {
  column-gap: 1.5em;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}