.filter-container {
  display: flex;
  align-items: center;
  // background: #fa9d9d;
  padding: 0.5em;
  justify-content: center;
  column-gap: 2em;
  .filters {
    display: flex;
    .compare-filters {
      display: flex;
      flex-direction: row;
      column-gap: 0.5em;
    }
    .date-filters {
      display: flex;
      column-gap: 0.5em;
    }
    .filter-input {
      padding: 0.5em;
      border-radius: 6px;
      border: 1px solid #00000091;
      font-size: 0.85em;
      margin-left: 5px;
      //     font-family:'Times New Roman', Times, serif
    }
    .error {
      border: 1px solid #ff2e00;
    }
  }
}
