.point-chart-container {
  display: flex;
  flex-direction: column;
  // align-items: center;
  justify-content: center;
  width: 100%;

  .header {
    display: flex;
    padding: 0em 1em;
    justify-content: center;
    align-items: center;
    .annotation {
      // flex: 0.3;
      font-weight: 600;
    }
    .filter {
      flex: 1;
    }
    .filters {
      display: flex;
      row-gap: 0.5em;
      // flex-direction: column;
      column-gap: 1em;
      align-items: center;
      .filter-input-container {
        display: flex;

        row-gap: 0.5em;
        flex-direction: column;
        justify-content: space-between;
        .input-div {
          display: flex;
          justify-content: space-between;

          .filter-input {
            padding: 0.5em;
            border-radius: 6px;
            border: 1px solid #00000091;
            font-size: 0.85em;
            margin-left: 5px;
          }
          .compare {
            width: 90%;
          }
        }
      }
    }
  }

  .header.hideIt {
    display: none;
  }
}
