.aco-view-conatiner {
  display: flex;
  column-gap: 1em;
  flex-wrap: wrap;
  row-gap: 1em;
  justify-content: space-between;
  overflow: auto;
  padding: 0.5em;

  .view-history { 
    text , path, rect, circle {
      cursor: pointer;
    }
    text:hover , path:hover, rect:hover, circle:hover {
      //fill: #1a116b;
      cursor: pointer;
    }
  }

}
