@-webkit-keyframes bg-red-50--blink {
  from {
    background-color: red;
  }
  to {
    background-color: inherit;
  }
}
@-moz-keyframes bg-red-50--blink {
  from {
    background-color: red;
  }
  to {
    background-color: inherit;
  }
}
@-o-keyframes bg-red-50--blink {
  from {
    background-color: red;
  }
  to {
    background-color: inherit;
  }
}
@keyframes bg-red-50--blink {
  from {
    background-color: var(--red-50);
  }
  to {
    background-color: inherit;
  }
}
.bg-red-50--blink {
  -webkit-animation: bg-red-50--blink 1s infinite; /* Safari 4+ */
  -moz-animation: bg-red-50--blink 1s infinite; /* Fx 5+ */
  -o-animation: bg-red-50--blink 1s infinite; /* Opera 12+ */
  animation: bg-red-50--blink 1s infinite; /* IE 10+ */
}

