.mainContentContainer {
  animation: fadeIn 1s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  // flex-wrap: wrap;
  // min-width: 50vw;

  .contentContainer {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 1em;
    .typeSelect {
      width: 30%;
      margin: 0em 1em;
    }
    .fileSelect {
      width: 30%;
      margin: 0em 1em;
      input {
        display: block;
        width: 100%;
        padding: 0.5em;
        font-size: 0.9375rem;
        font-weight: 400;
        line-height: 1.53;
        /* color: #697a8d; */
        background-color: #fff;
        background-clip: padding-box;
        /* border: 1px solid #d9dee3; */
        appearance: none;
        border: 1px solid #bababa;
        border-radius: 0.375rem;
        transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      }
      input::file-selector-button {
        // background-color: red;
        border: none;
        border-right: 1px solid #bababa;
        padding: 10px;
      }
    }
    .btnContainer {
      width: 20%;
    }
  }
  .tableContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    table {
      width: 100%;
      border-spacing: 0;
      border: 1px solid #f2efef;
    }
    th,
    td {
      border: 1px solid #f2efef;
      text-align: center;
      // padding: 1em;
    }
    th {
      background-color: #f4f0f0;
    }
    tbody {
      display: block;
      max-height: 10em;
      overflow: auto;
    }
    thead,
    tbody tr {
      display: table;
      width: 100%;
      table-layout: fixed;
    }
    td:last-child,
    th:last-child {
      width: 6em;
      overflow: hidden;
      white-space: nowrap;
    }
  }
}

.attach-popup-actions {
  column-gap: 1.5em;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.attachment-popup__table__file-name {
  text-overflow: ellipsis;
  overflow: hidden;
  // display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
