.reports-conatiner {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas:
    "pageHeader pageHeader pageHeader"
    "filters filters filters"
    "tables eachStationChart eachTypeChart"
    "tables eachStationByTypeChart eachStationByTypeChart";
  grid-gap: 0.1em;
  grid-template-rows: 0.1fr 1fr 2.2fr 5fr;
  grid-template-columns: 1.3fr 1.5fr 1.5fr;
  position: relative;
  .reportsCard {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    max-height: 100vh;
    max-width: 100vw;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    // padding: 0.3em;
  }
  .tableBox {
    position: absolute;
    width: 100%;
    height: 100%;
    .data-table {
      height: 100% !important;
    }
    .data-table {
      .MuiDataGrid-toolbarContainer {
        .MuiButton-root {
          font-size: 0.7125rem;
          .css-y6rp3m-MuiButton-startIcon > *:nth-of-type(1) {
            font-size: 14px;
          }
        }
        .MuiInputBase-inputTypeSearch {
          width: 120px;
        }
        .MuiInputBase-inputTypeSearch + .MuiIconButton-root {
          display: none;
        }
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 1.2rem;
        }
      }
    }
    ::-webkit-scrollbar {
      width: 5px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
      // box-shadow: inset 0 0 5px grey;
      border-radius: 5px;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: grey;
      border-radius: 5px;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: #b30000;
    }
  }
  .filterBox {
    position: absolute;
    grid-area: filters;
    width: 100%;
    display: flex;
    column-gap: 0.5em;
    align-items: center;
    // justify-content: center;
    row-gap: 0.4em;
    flex-wrap: wrap;
    padding: 0.2em;
    .filter-input {
      flex: 1;
      // max-width: 11em !important;
      min-width: 13em !important;
      // max-width: 13em !important;
    }
    // .auto-complete {
    //   flex: 1;
    //   min-width: 24em !important; // auto
    // }
  }
  .tableBox {
    grid-area: tables;
  }
  .eachStationChartBox {
    grid-area: eachStationChart;
    // min-width: 35em;
    // max-height: 10em;
  }
  .eachTypeChartBox {
    grid-area: eachTypeChart;
  }
  .pageHeader {
    grid-area: pageHeader;
    font-weight: bold;
    font-size: 1.1em;
  }
  .eachStationByTypeChartBox {
    grid-area: eachStationByTypeChart;
  }
}
.gridDevice {
  grid-template-columns: 1.6fr 1.6fr 1.6fr;
  grid-template-areas:
    "filters filters filters"
    "tables eachTypeChart eachTypeChart"
    "tables eachStationByTypeChart eachStationByTypeChart";
}
