.liveClockTooltip {
  font-size: 1.2em;
  //   background-color: red;
}
.liveStatusClock {
  margin-right: 1rem;
  //   border: 1px solid black;
  display: flex;

  padding: 0 0.5em;
  background-color: rgb(109 109 109);
  //   font-weight: bold;
  font-size: 1.1em;
  color: rgb(255, 255, 255);
  border-radius: 0.3em;
  cursor: default;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  justify-content: center;
  align-items: center;
  column-gap: 0.3em;
  //   -webkit-user-select: none; /* Safari */
  //   -ms-user-select: none; /* IE 10 and IE 11 */
  //   user-select: none;
  .blinkIcon {
    width: 0.5em;
    height: 0.5em;
    background-color: #05ff05;
    border-radius: 1em;
  }
  .blinkAnimation {
    animation: blink 0.5s linear infinite;
  }
  @keyframes blink {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}
.isOffline {
  opacity: 0.5;
  .blinkIcon {
    // width: 0.5em;
    // height: 0.5em;
    background-color: red;
    // border-radius: 1em;
  }
  cursor: not-allowed;
}
