.payload-view-conatiner {
  display: flex;
  column-gap: 1em;
  flex-wrap: wrap;
  row-gap: 1em;
  justify-content: flex-start;
  flex-direction: column;
  overflow: auto;
  padding: 0.5em;
  background: #eee;
  //overflow: hidden;

  .payload-view {
    display: flex;
    flex-direction: column;

    padding: 10px;

    .row {
      //padding-bottom: 16px;
    }

    //flex-wrap: wrap;
    // div {
    //   padding: 5px 15px 5px 15px;
    //   border: 1px solid #777;
    //   border-radius: 5px;
    //   background: #ccc;
    //   margin: 5px;
    //   min-width: 200px;
    // }

    .row_1 {
      display: flex;
      flex-direction: row;
      padding-bottom: 25px;

      svg.qrCode {
        height: auto;
        width: 75px;
        max-width: 75px;
        margin: 0px 10px 5px 0px;
      } 

      .healthIcon {
        max-width: 30px;
        max-height: 30px;
        margin-left: 60px;

        SVG {
          fill: #f00;
        }
        &.fail SVG {
          fill: rgb(255 0 0 / 55%)
        }

        &.success  SVG {
          fill: #0a9b0a;
        }

      }

      .row_2 {
        display: flex;
        flex-direction: column;
        margin: 0px 10px 5px 10px;

        h5 {
          font-weight: bold;
        }

        .rtu_icons {
          height: auto;
          width: 150px;
          padding: 10px 10px 10px 0px ;  
          margin-left: -10px;
        }
        .icon_title {
          padding-left: 25px;
        }

        .point-chart-container {
          padding-left: 15px;
        }

        table.info_pm {
          tr {
            td {
              padding-right: 25px; 
            }
          }


          p {
            margin: 0px;
            padding: 0px;
          }
        }
        table.info_pm_vib {
          td {
            padding: 4px 10px 4px 4px; 
          }
        }
      }

    }


  }

  .viberation-view {
    min-width: 1000px;   
  } 
}
