.acdb-view-container {
  width: 97%;
  height: 100%;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 8px;
  background: #ffffff;
  overflow: auto;
  display: flex;
  // justify-content: center;
  align-items: left;
  flex-direction: column;
  position: relative;
  .acdb-header {
    position: sticky;
    top: 0;
    left: 0;
  }

  .snapshot-title {
    padding: 10px;
    font-size: 12px;
    font-weight: bold;
  }
  .print-icon {
    position: absolute;
    right: 1%;
    top: 0px;
    // align-self: self-end;
  }

  .ACDB_1_STN {
    width: 100%;
    height: 90%;
  }
  .ACDB_2_STN {
    // height: 60%;
    // display: block;
  }
  .ACDB_3_STN {
    width: 100%;
    height: 90%;
  }

  .view-history {
    text,
    path,
    rect,
    circle {
      cursor: pointer;
    }
    text:hover,
    path:hover,
    rect:hover,
    circle:hover {
      //fill: #1a116b;
      cursor: pointer;
      opacity: 0.7;
    }
  }
  .view-history:hover {
    //fill: #1a116b;
    cursor: pointer;
    opacity: 0.7;
  }
}

