.relay-search-container {
  display: flex;
  flex-direction: column;
  gap: 1em;
  // justify-content: ;
  width: 100%;
  height: 100%;
  .relay-search-header {
    // height: 10%;
    .timeline-filter-container {
      height: 100%;
      width: 100%;
      display: flex;
      // align-items: center;
      row-gap: 0.5em;
      column-gap: 1em;
      flex-wrap: wrap;
      .timeline-filter {
        // flex: 1;
        min-width: 13em;
      }
      .timeline-filter-actions {
        display: flex;
        height: fit-content;
        gap: 1em;
      }
    }
  }
  .relay-search-table-container {
    height: 100%;
    background: #ececec;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4em;
    font-weight: 600;

    .relay-search-table {
      .timeline-cell {
        display: flex;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .timeline-cell-connector {
          flex: 1;
          border: 1px solid !important;
        }
        .timeline-cell-circle {
          width: 1em;
          height: 1em;
          border-radius: 50%;
          background-color: #656565;
        }
        .error {
          background-color: red;
        }
        .success {
          background-color: #0f0;
        }
      }
      .event-cell {
        font-weight: bolder;
        display: flex;
        column-gap: 0.5em;
        justify-content: space-between;
        align-items: center;

        .status_icon {
          width: 12px;
          height: 12px;
          // background-color: #0f0;
          border-radius: 50%;
          border: 1.5px solid black;
        }
        .status-fail {
          background-color: red;
        }
        .status-success {
          background-color: #0f0;
        }
      }
      .pss_event_cell {
        flex-direction: column;
        align-items: baseline;
        .pss_event {
          display: flex;
          justify-content: center;
          align-items: center;
          column-gap: 0.4em;
        }
      }

      .relay-event-cell {
        display: flex;
        column-gap: 0.5em;
        align-items: center;
        min-width: 7em;
        // justify-content: space-between;
        .event-icon {
          border-radius: 50%;
          width: 2em;
          height: 2em;
          text-align: center;
        }
      }
      [data-rowindex="0"] {
        .timeline-cell-connector-first {
          border: none;
        }
      }
      .MuiDataGrid-row--lastVisible {
        .timeline-cell-connector-last {
          border: none;
        }
      }
    }

    .backGreen {
      background-color: #0f0;
    }
    .backRed {
      background-color: #ff4e4e;
    }
  }
}
