.assets-information-popup {
  h3,
  h4 {
    margin: 0;
    padding: 0;
  }
  a {
    text-decoration: none;
  }
  ul {
    padding: 0;
    margin: 0;
  }

  li {
    border-bottom: 1px solid rgb(218, 218, 218);
  }
  .assets-info-popup-close-icon {
    margin-left: auto;
  }
  .assets-info-popup-content-container {
    row-gap: 1em;
    height: 35em;
    .assets-info-popup-navigation-tabs {
      column-gap: 5px;
      height: max-content;
      padding-left: 5px;
      .info-popup-navigation-tab {
        border-radius: 1px;
        padding: 0.5em;
        background-color: #eceef1;
        color: #233446;
      }
      .info-popup-navigation-tab-active {
        background-color: #233446;
        color: #fff;
      }
    }
    .assets-info-popup-data-container {
      column-gap: 1em;
      flex-basis: 100%;
      overflow-y: auto;
      padding: 5px;
      .assets-info-popup-data-info {
        box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
        overflow-y: auto;
        .assets-info-popup-tab-view-container,
        ul,
        .assets-info-popup-tab-data {
          animation: fadeIn 0.5s;
        }
        .assets-info-popup-tab-view-container {
          height: 100%;
          .assets-info-popup-tab-sections {
            box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
            height: 100%;
            overflow-y: auto;
            max-width: 40%;
            .info-popup-tab {
              background-color: #fff;
            }
            .info-popup-tab-active {
              background-color: #eceef1;
            }
          }
          .assets-info-popup-tab-data {
            height: 100%;
            overflow-y: auto;
          }
        }
        // .assets-info-popup-accordion-container {
        //   box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
        //   .assets-info-popup-accordion:hover {
        //     background-color: rgb(248, 248, 248);
        //   }
        // }
        .assets__info-popup__section {
          .assets__info-popup__section--title {
            display: flex;
            justify-content: center;
            border-bottom: 1px solid rgba(0, 0, 0, 0.3);
          }
          // background-color: lightgray;
          padding: 1em;
          row-gap: 1em;
          max-height: -moz-fit-content;
          max-height: fit-content;
          display: flex;
          flex-direction: column;
          box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
          border-radius: 10px;
          flex: 8 1;
          background: rgba(238, 238, 238, 0.5882352941);
          border: 1px solid rgba(0, 0, 0, 0.2392156863);
          margin: 1rem 0.5rem;
        }
        .object-data-viewer-list-item {
          padding: 2px 0.5em;
          .object-data-viewer-list-item-key {
            font-weight: 600;
            border-radius: 1em;
            color: #3a5d83;
            flex: 1;
          }
          .object-data-viewer-list-item-separator {
            margin-right: 1em;
          }
          .assets__info__attachment--link {
            color: #1976d2;
            transition: 0.3s;
            &:hover {
              color: #0b4681;
            }
          }
          .object-data-viewer-list-item-value {
            flex: 1;
            a {
              color: #1976d2;
            }
          }
        }
      }
    }
    .assets-info-popup-attachment-info-container {
      box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
      // width: max-content;
      // height: max-content;
      // max-width: 40%;
      width: 40%;
      height: max-content;
      max-height: 100%;
      padding: 1em;
      .assets-info-popup-image {
        img {
          animation: fadeIn 1s;
        }
      }

      .assets-images-preview-container {
        .assets-images-preview-previous-navigation,
        .assets-images-preview-forward-navigation {
          color: white;
          background: #a5a5a5;
          height: 30px;
          width: 30px;
          margin: 10px;
          font-size: 20px;
          border-radius: 2px;
          border: medium none;
          &:hover {
            background: rgb(117, 117, 117);
          }
        }
        .assets-images-preview-previous-navigation::before {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          content: "\003c";
        }
        .assets-images-preview-forward-navigation::before {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          content: "\003e";
        }
      }
    }
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}
