.tc-utilization {
  display: grid;
  width: 100%;
  height: 100%;
  grid-gap: 10px;
  grid-template-rows: 0.1fr 1fr 5fr 5fr;
  grid-template-columns: 3fr 1fr 3fr;
  position: relative;
  grid-template-areas:
    "pageHeader pageHeader pageHeader"
    "filters filters filters"
    "count-chart table table "
    "count-chart runTimeChart runTimeChart";

  .card {
    border-radius: 5px;
    max-height: 100vh;
    max-width: 100vw;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // flex-direction: column;
    // border-bottom: 3px solid #71717a;

    .card-heading {
      font-weight: bold;
      font-size: 1em;
    }
  }
  .pageHeader {
    grid-area: pageHeader;
    font-weight: 600;
    font-size: 1.2em;
  }
  .utilisation-filter {
    grid-area: filters;
    padding: 0.3em;
    display: flex;
    width: 100%;
    height: 100%;
    column-gap: 0.5em;
    align-items: center;
    position: absolute;
    .auto-complete {
      min-width: 20em !important; // auto
    }
    // justify-content: end;
    // .filter-input {
    //   flex: 1;
    //   // max-width: 11em !important;
    //   min-width: 14em !important;
    //   // max-width: 13em !important;
    // }
  }
  .fullScreenCountChart {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    max-height: 100vh;
    z-index: 1200;
  }
  .count-chart {
    grid-area: count-chart;
    overflow-y: auto;
    // max-height: 51em;
    .card-header {
      display: flex;
      justify-content: space-between;
      padding: 0em 0.2em;
      position: sticky;
      top: 0;
      background: white;
      .fullScreenIcon {
        color: #1976d2;
      }
    }
  }
  .tcRuntimeChart {
    grid-area: runTimeChart;
    display: flex;
    flex-direction: column;
    .card-header {
      // flex: 1;
      display: flex;
      justify-content: space-between;
      padding: 0em 0.2em;
      position: sticky;
      top: 0;
      background: white;
      .fullScreenIcon {
        color: #1976d2;
      }
    }
    // .chart-container{
    //   // flex: 9;
    //   width: 100%;
    //   height: 90%;
    // }
  }
  .mostUtilisedTable {
    grid-area: table;
    padding: 0.3em;
    .header {
      display: flex;
      justify-content: space-between;
      padding: 0em 1em;
      .countSelect {
        display: flex;
        column-gap: 1em;
      }
    }
    .data-table {
      height: 90% !important;
    }
    .data-table {
      .MuiDataGrid-toolbarContainer {
        .MuiButton-root {
          font-size: 0.7125rem;
          .css-y6rp3m-MuiButton-startIcon > *:nth-of-type(1) {
            font-size: 14px;
          }
        }
        .MuiInputBase-inputTypeSearch {
          width: 120px;
        }
        .MuiInputBase-inputTypeSearch + .MuiIconButton-root {
          display: none;
        }
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 1.2rem;
        }
      }
    }
  }
}
