.er-view-conatiner {
  display: flex;
  column-gap: 1em;
  flex-wrap: wrap;
  row-gap: 1em;
  justify-content: space-evenly;
  overflow: auto;
  padding: 0.5em;

  .svg_block {
    border: 1px solid #999;

    .header_label {
      padding: 5px;
      background: #ccc;
      font-weight: bold;
      color: #4d5256;
      display: flex;
      justify-content: space-between;
    }
    .view-history {
      text,
      path,
      rect,
      circle {
        cursor: pointer;
      }
      text:hover,
      path:hover,
      rect:hover,
      circle:hover {
        fill: #1a116b;
        cursor: pointer;
        opacity: 0.7;
      }
    }
  }
}
.new-er-view-conatiner {
  display: flex;
  column-gap: 1rem;
  flex-wrap: wrap;
  row-gap: 1rem;
  justify-content: space-evenly;
  overflow: auto;
  padding: 0.5em;

  .svg_block {
    border: 1px solid #999;
    // width: 50%;
    // .ER_SVG {
    //   width: 100%;
    // }
    .header_label {
      padding: 5px;
      background: #ccc;
      font-weight: bold;
      color: #4d5256;
      display: flex;
      justify-content: space-between;
    }
  }
}

@media print {
  .new-er-view-conatiner {
    overflow: visible;
    height: fit-content;
    gap: 2rem;
  }
}

.ersHistoryDisabled {
  opacity: 0.4;
  cursor: not-allowed;
}
