.fiu-fdsHistory-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  .fiuFdsHistoryHeader {
    display: flex;
    width: 100%;
    justify-content: space-between;

    .assetName {
      font-size: 1.1em;
      font-weight: 600;
    }
    .dateFilterContainer{
display: flex;
column-gap: 1em;
    }
  }
  .fiuFdsTableHistory {
    height: 100%;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
    padding: 0.5em;
    position: relative;
    width: 100%;
  }
}
