.pm-utilisation-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-gap: 10px;
  grid-template-rows: 0.1fr 1fr 6fr 6fr;
  grid-template-columns: 4fr 3fr 3fr;
  position: relative;
  grid-template-areas:
    "pageHeader pageHeader pageHeader"
    "filters filters filters"
    "count-chart table table "
    "count-chart total-runtime-chart average-runtime-chart";
  // grid-template-areas:
  //   "filters filters "
  //   "count-chart total-runtime-chart "
  //   "count-chart avg-runtime-chart ";
  // grid-template-areas:
  //   "filters filters "
  //   "count-chart count-chart "
  //   "total-runtime-chart avg-runtime-chart ";
  .card {
    border-radius: 5px;
    max-height: 100vh;
    max-width: 100vw;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // flex-direction: column;
    // border-bottom: 3px solid #71717a;

    .card-heading, .card-heading span  {
      font-weight: bold;
      font-size: 1em;
    }
  }
  .count-chart {
    grid-area: count-chart;
    overflow-y: auto;
    // max-height: 51em;
    .card-header {
      display: flex;
      justify-content: space-between;
      padding: 0em 0.2em;
      position: sticky;
      top: 0;
      background: white;
      .fullScreenIcon {
        color: #1976d2;
        display: flex;

        .fullscreensvg {
          margin: 6px 7px 0px 14px
        }
      }
    }
  }
  .pageHeader {
    grid-area: pageHeader;
    font-weight: 600;
    font-size: 1.2em;
  }
  .utilisation-filter {
    grid-area: filters;
    padding: 0.3em;
    display: flex;
    width: 100%;
    height: 100%;
    column-gap: 0.5em;
    align-items: center;
    position: absolute;
    // justify-content: end;
    // .filter-input {
    //   flex: 1;
    //   // max-width: 11em !important;
    //   min-width: 14em !important;
    //   // max-width: 13em !important;
    // }
    .auto-complete {
      min-width: 20em !important; // auto
    }
  }
  .runtime-chart-card {
    position: absolute;
    // grid-area: runtime-chart;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    .card-header {
      display: flex;
      justify-content: space-between;
      width: 100%;
      .card-heading {
        height: 10%;
      }
      .fullScreenIcon {
        color: #1976d2;
        display: flex;

        .fullscreensvg {
          margin: 6px 7px 0px 14px
        }
      }
    }
    .chart-container {
      height: 90%;
      width: 90%;
    }
  }
  .fullScreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    z-index: 1200;
  }
  .fullScreenCountChart {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    max-height: 100vh;
    z-index: 1200;
  }
  .Total_runtime {
    grid-area: total-runtime-chart;
  }
  .Average_runtime {
    grid-area: average-runtime-chart;
  }
  .Total_operations{
    grid-area: total-runtime-chart;
  }
  // .total-runtime-chart {
  //   grid-area: total-runtime-chart;

  // }
  // .avg-runtime-chart {
  //   grid-area: avg-runtime-chart;
  // }
  .mostUtilisedTable {
    grid-area: table;
    padding: 0.3em;
    .header {
      display: flex;
      justify-content: space-between;
      padding: 0em 1em;
      .countSelect {
        display: flex;
        column-gap: 1em;
      }
    }
    .data-table {
      height: 93% !important;
    }
    .data-table {
      .MuiDataGrid-toolbarContainer {
        .MuiButton-root {
          font-size: 0.7125rem;
          .css-y6rp3m-MuiButton-startIcon > *:nth-of-type(1) {
            font-size: 14px;
          }
        }
        .MuiInputBase-inputTypeSearch {
          width: 120px;
        }
        .MuiInputBase-inputTypeSearch + .MuiIconButton-root {
          display: none;
        }
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 1.2rem;
        }
      }
    }
  }
}
