.users {
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 5px;
  padding: 0.5em;
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  row-gap: 0.6em;
  .create-user-button {
    margin: 0 8px 0 auto;
  }
}

.overflow-tooltip {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  > div {
    margin-right: 0.25rem;
  }
}
.overflow-tooltip-hover {
  > div {
    margin-right: 0.25rem;
    margin-top: 0.25rem;
    background-color: #4285f4 !important;
    color: #fff;
  }
}
