.rack-unique-view-conatiner {
  display: flex;
  column-gap: 1em;
  flex-wrap: wrap;
  row-gap: 1em;
  justify-content: space-between;
  overflow: auto;
  padding: 0.5em;

  .MuiGrid-container {
    

    .MuiGrid-item {
      position: relative;
    }
    
    .rack-unique-view-items-container {
      display: flex;
      justify-content: center;
          
      .rack-unique-view-item {
        padding: 5px;
        border: 1px solid rgb(43, 83, 55);        
        min-width: 100px;
        border-radius: 5px;
        position: relative;

        div {
          border: 1px solid #727972;
          width: 108%;
          margin: 0px 0px 0px 0px;
          position: absolute;
          left: -3%;
          top: 125%;
        }
      }
      .rack-unique-view-item.success {
        background: #8fc18f;
      }
      .rack-unique-view-item.failure {
        background: #eb5f4e;
      }      
    }

    .rack-unique-view-items-title {
      text-align: center;
      padding: 18px 0px 6px 0px;
    }

    .rack-unique-view-grouped-line {
      border: 1px solid black;
      width: 1px;
      height: 10px;
      text-align: center;
      position: absolute;
      left: 50%;
      top: 59px;
    }

  }
}
