.gis-conatiner {
  display: flex;
  width: 100%;
  height: 100%;
  row-gap: 0.5em;
  flex-direction: column;

  .gis-filters {
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    padding: 0.3em;
    flex: 0.7;
    display: flex;
    justify-content: space-between;
    padding: 0.4em 1em;
    align-items: center;
    row-gap: 0.3em;
    flex-wrap: wrap;
    min-width: fit-content;
    .filter-inputs {
      display: flex;
      column-gap: 0.5em;

      .assetType {
        min-width: 25em !important;
      }
    }
    .find-asset-inputs {
      display: flex;
      column-gap: 0.5em;
    }
    .filter-input {
      flex: 1;
      min-width: 15em;
    }
  }
  .map-container {
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    // padding: 1em;
    flex: 9;
  }
}
.custom-marker-icon {
  width: 32px;
  height: 32px;
}

