.login-page {
  p {
    margin: 0 !important;
  }
  .footer-copyright {
    align-items: center !important;
    display: flex !important;
    justify-content: center !important;
    padding-top: 1em !important;
    img {
      width: 30px !important;
      margin-right: 5px;
    }
  }
  .hitachiLogo_login {
    width: 100px;
    position: absolute;
    left: 15px;
    top: 15px;
  }
