.form-creator-main-body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 0;
  align-items: center;
  row-gap: 0.3em;
  .form-creator-header {
    width: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgb(0 0 0 / 20%);
    background-color: #ffffff;
    padding: 0.5em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
      border-radius: 10px;
    // .Form-creator-heading {
    //   display: flex;
    //   font-size: 1.2em;
    //   font-weight: bold;
    //   padding: 5px;
    // }
    // // .Form-creator-heading:hover {
    // //   background-color: #e4e4e4;
    // // }
    // .Form-creator-heading[contentEditable=true] {
    //   background-color: #e4e4e4;
    //   border: 1px solid black;
    // }
    .form-fields{
      display: flex;
      column-gap: 1em;
      .auto-complete{
        min-width: 15em !important;
      }  
    }
    .formCreaterHeadingInput{
      font-size: 1.5em;
      border: 0;
      // font-weight: bold;
      padding: 0.2em;
      /* color: wheat; */
      border-radius: 4px;
      background: #dadada;
    }
    
    .formCreaterHeadingInput::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-weight: 100;
  
      }
 
  
  }
  .form-creator-body {
    display: flex;
    height: 85%;
    width: 70%;
  }
  .form-creator-footer {
    display: flex;
    width: 70%;
    justify-content: space-between;
  }
}
