.popOverContainer{
    .css-3bmhjh-MuiPaper-root-MuiPopover-paper{
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    font-size: 15px;
    font-family: math;
    border: 1px solid #bcbcbc;
    font-weight: bold;
    background-color: rgb(243, 243, 243);
    color: black;
    // padding: 3px 0px;

    }
    .label{
      border-bottom: 1px solid #835858;
        padding: 4px;
    }
    .activeSubTab{
        background-color: #1976d2;
        font-weight: 700;
        color: #ffff;
    }
    .label:last-child{
        border-bottom: none;
    }
    .label:hover{
        transition: linear 0.2s;
        background-color: #1976d2;
        font-weight: 700;
        color: #ffff;
        cursor: pointer;
    }
}