
.form-preview-container {
  display: flex;
  // row-gap: 1em;
  flex-direction: column;
  width: 100%;
  flex: 5;
  border: 1px solid rgb(0 0 0 / 20%);
  background-color: #ffffff;
  padding: 1em;
  overflow-y: auto;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 10px;
    font-family: system-ui;
    .filed-row{
      display: flex;
      width: 100%;
      padding: 0.5em;
      column-gap: 0.5em;
      .fieldHandle{
        cursor: grab;
      }
      .scetionField{
        background: #eeeeee96;
        border: 1px solid #0000003d;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
        border-radius: 10px;
        max-height: fit-content;
        padding: 1em;
      }
      .editToolBox{
        display: none
        ;
      }
      .heading {
        margin: 0;
      }
      .sub-heading {
        margin: 0;
      }
     .field-item{
      flex: 8;
     }
     .scetionField{
      .header{
        font-weight: 600;
        font-size: 1em;
      }
     }
      // .form-section {
      //   padding: 1em;
      //   row-gap: 0.5em;
      //   min-height: 3em;
      //   box-shadow: rgb(254 254 254) 1px 2px 2px;
      // }
    }
    .selectedFieldRow{
      border-radius: 0.3em;
      border: 2px dotted black;

      .editToolBox{
        display: flex
        ;
      }
    }
    .hideField{
      display: none;
    }
    .fieldDragging{
      // background-color: red;
      opacity: 0.1;
      background-color: rgb(211, 205, 205);
      border-radius: 0.3em;
    }

}
