.main-container {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 5px;
  padding: 0.5em;
  width: 100%;
  height: 100%;
  row-gap: 0.6em;
  .registerTabs {
    display: flex;
    flex-direction: row;
    margin-bottom: 1em;
    .tab {
      margin: 0px 0.5em;
      padding: 0.5em;
      background-color: #c1bbbb38;
      color: #1976d2;
      border-radius: 5px;
    }
    .active {
      color: white;
      background-color: #1976d2;
      // border-bottom: 3px solid #d1cccc;
    }
  }
  .tab-des {
    .tab-content {
      display: none;
      // .tabFormArea {

      //   max-height: 64vh;
      //   overflow: auto;
      //   padding: 0em;
      // }

      // #btnGenralForm {
      //   padding: 1em 5em;
      // }
      // #btnAdditionalForm {
      //   padding: 1em 1em;
      // }
    }
    .active {
      display: block;
      .registerSubTabs {
        display: flex;
        flex-direction: row;
        margin-bottom: 1em;
        .subTab {
          margin: 0px 0.5em;
          padding: 0.5em;
          background-color: #c1bbbb38;
          color: #1976d2;
          border-radius: 5px;
        }
        .activeSubTab {
          color: white;
          background-color: #1976d2;
        }
      }
      .subTab-des {
        margin-top: 1em;
        .subTab-content {
          display: none;
          // .subTabFormArea {
          //   max-height: 57vh;
          //   overflow: auto;
          //   padding: 0em;
          // }
        }
        .activeSubTab {
          display: block;
        }
      }
    }
  }
}
.assets__asset-register__form {
  h2,
  h4,
  .assets__asset-register__form__field--attachment {
    grid-column: span 2;
    margin: 0;
  }
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding: 1em;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  border-radius: 10px;
  background-color: rgb(250, 250, 250);
  border: 1px solid rgba(0, 0, 0, 0.2392156863);
}

.asset-register__asset-form--section {
  grid-column: span 2;
  // row-gap: 1em;
