.alarm-rules--container {
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 5px;
  padding: 0.5em;
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  row-gap: 0.6em;
}

.rule--section {
  background: #fff;
  padding: 0.375rem 1rem;
  border-radius: 0.5rem;
  margin: 1rem auto;
  transition: 0.3s;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 3px 0px;
  h4 {
    margin: 1.5rem 0 0.5rem 0;
  }
  p {
    margin: 0;
  }
}
.rule-static-fields--wrapper {
  > div {
    margin: 0.5rem;
  }
  p {
    min-width: 7rem;
  }
}
.rule-dynamic-fields--wrapper {
  margin-top: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}
.rule-field {
  margin: 0.25rem 0;
  padding: 0.5rem;
  display: flex;
  flex: 1 1 40rem;
  align-items: center;
  gap: 0.5rem;
}
.rule-field--dynamic {
  border: 1px solid #f5f4f2;
  box-shadow: rgba(0, 0, 0, 0.375) 0px 0px 1px 0px;
}
.remove-dynamic-field {
  line-height: 0;
}
.rule-action-button--wrapper {
  width: 100%;
  transition: 0.3s;
  margin: 1rem auto;
  display: flex;
  justify-content: flex-end;
}
.rule--section-condition-wrapper {
  margin: 1rem 0;
}
.rule-field-error-message {
  color: red;
}
.rule-field-add-type {
  margin: 0.5rem 0 !important;
}
.alarm-rules__copy__select-rules--container {
  background-color: #e9e9e9;
  padding: 0.25rem 0.5rem;
  border-radius: 3px;
  .alarm-rules__copy__select-rules--list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

/* Media Queries */
@media (min-width: 768px) {
  .rule--section,
  .rule-action-button--wrapper {
    width: 90%;
  }
}
@media (min-width: 1024px) {
  .rule--section,
  .rule-action-button--wrapper {
    width: 75%;
  }
}
@media (min-width: 1280px) {
  .rule--section,
  .rule-action-button--wrapper {
    width: 65%;
  }
}
@media (min-width: 1536px) {
  .rule--section,
  .rule-action-button--wrapper {
    width: 50%;
  }
}
