.station-bottom-view-conatiner {
  position: fixed;
  width: 100%;
  bottom: 15px;
  padding: 4px 160px 10px 25px;
  display: flex;
  justify-content: space-between;
  background: #363030;
  opacity: 85%;
  // height: 30%;
  column-gap: 0.5em;
  // padding: 0.5em;
  min-width: 700px;
  // max-height: 115px;
  margin: 0px 0px 0px -10px;
  .station-bottom-view-child {
    display: flex;
    column-gap: 1em;
    align-items: center;
    .box-lable {
      font-size: 1.5em;
      color: #fff;
    }
    .box-content {
      display: flex;
      flex-direction: column;
      row-gap: 0.3em;

      .temp-view {
        display: flex;
        column-gap: 0.5em;
      }
      .btn-groups {
        display: flex;
        flex-wrap: wrap;
      }

      .smoke-temp-label {
        color: #fff;
      }
    }
  }
  .pss-view {
    flex: 3.5;
  }
  .ser-view {
    flex: 7;
  }
  .asm-view {
    flex: 2;
  }
