  button {
    background: #097809;
    color: #fff;
    margin: 1px;
    padding: 1px;
  }

  button.circleButton {
    border-radius: 10px;
    height: 20px;
    min-width: 20px;
    margin: 5px 10px 5px 0px;
    font-size: 8px;
  }
  button.serTempRed {
    background-color: #f00;
  }
  button.serTempGreen {
    background-color: #268f26;
  }
  button.smoke_failure {
    background: #f00;
  }
  button.smoke_normal {
    background: #097809;
  }

  button.circleButton.info {
    background: #097809;
  }

  button.failure {
    background: #d6412e;
  }
  button.success {
    background: #268f26;
  }
  button.grayedout {
    background: #696d69;
  }

  .asm-view {
