.filter-wrapper-conatiner {
  background: #bebebe;
  padding: 0.2em;
  display: flex;
  width: 100%;
  .filter-wrapper {
    max-height: 10em;
    transition: all 0.5s ease;
    overflow: hidden;
    width: 100%;
  }

  .filter-wrapper.minimized {
    max-height: 0.2em;
    overflow: hidden;
  }
  .filterIcon {
    cursor: pointer;
    border-radius: 10%;
  }
  .filterIcon:hover {
    background-color: #ececec;
  }
}
