.form-page {
  display: flex;
  justify-content: center;
  flex-direction: column;
  // align-items: center;
  // align-self: center;
  margin: 0px auto;
  min-width: 30em;
  width: 70%;
  height: 80%;
  padding: 1em;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px, rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  background-color: #ffffff;
  row-gap: 0.5em;
  border-radius: 1em;
  .form-section{
    display: flex;
    flex-direction: column;
    row-gap: 0.5em;
    .part-header {
      padding: 0.2em;
      font-family: monospace;
      font-size: 19px;
      font-weight: bold;
      color: tomato;
    }
    .form-part{
      display: flex;
      // justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      row-gap: 0.5em;
      column-gap: 0.5em;
      .field{
        min-width: 20em  !important;
        flex: 1;
        // max-width: 50em !important;
      }
    }
  }
  .footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .horizontal-rule {
    height: 1px;
    color: black;
    width: 100%;
  }
