.devices {
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 5px;
  padding: 0.5em;
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  row-gap: 0.2em;
  .devices-table-container {
    display: flex;
    flex-direction: column;
    row-gap: 0.4em;
    width: 100%;
    .data-table {
      height: 75vh !important;
    }
  }
  .asset-table-container {
    display: flex;
    flex-direction: column;
    row-gap: 0.3em;
    height: 100%;
    .maintenanceTabFilters {
      // border-radius: 5px;
      // background-color: #ffffff;
      // box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
      display: flex;
      column-gap: 0.5em;
      align-items: center;
      // justify-content: center;
      row-gap: 0.4em;
      flex-wrap: wrap;
      padding: 0.3em;
      flex: 1;
      .filter-input {
        flex: 1;
        // max-width: 11em !important;
        min-width: 14em !important;
        // max-width: 13em !important;
      }
    }
    .status-filter-count-btn-group {
      flex: 1;
    }
    .maintenanceTabTable {
      flex: 50;
      .data-table {
        height: 100% !important;
      }
    }
  }
  .roaster-table-container {
    .groupHeader {
      border-right: 1 px solid;
      border-right: 1px solid rgb(166 164 164);
      padding: 0 !important;
      background: #dde0e063;
    }

    .ackTextField {
      width: 100%;
    }
  }

  // .MuiDataGrid-row{
  .changeRowColor {
    background-color: #ffa50057;
  }
  .changeRowColor:hover {
    background-color: #ffa50057 !important;
  }
  // }
}
/* For Component AddDevices */
.add-device-form-close {
  margin-right: 1em;
}
.tooltip--title {
  font-size: 0.8rem;
}
