.asset-assignment-form-conatiner {
  display: flex;
  flex-direction: column;
  row-gap: 0.5em;
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 5px;
  height: 100%;
  .hedaer-inputs {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3em 1em;
    column-gap: 1em;
    width: 100%;
    .fields {
      flex: 9;
      display: flex;
      flex-direction: column;
      row-gap: 1em;
      .row {
        display: flex;
        justify-content: center;
        column-gap: 0.5em;
        .autocomplete-input {
          // max-height: 3.5rem;
          // overflow: hidden;
          width: 40%;
          height: 100%;
        }
      }
    }
    .addBtn {
      flex: 1;
    }
    // flex: 1;
    // padding: 0.5em;
    // .autocomplete-input {
    //   // max-height: 3.5rem;
    //   // overflow: hidden;
    //   width: 30%;
    //   height: 100%;
    // }
  }
  .preview-container {
    // border: 1px solid black;
    // min-height: 20em;
    // flex: 10;
    height: 90%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
    // justify-content: center;
    // table, td, th {
    //   border: 1px solid rgb(211 211 211);
    // }

    // table {
    //   border-collapse: collapse;
    //   width: 95%;
    //   thead{
    //     position: sticky;
    //     background-color: white;
    //     top: 0;

    //   }
    // }

    // td {
    //   text-align: center;
    //   padding: 0;
    // }

    table {
      width: 90%;
      border-spacing: 0;
      height: 100%;
      // border: 1px solid #080808;
      box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
      thead {
        position: sticky;
        background-color: white;
        top: 0;
      }
    }
    th,
    td {
      // border: 1px solid #6a6969;
      // padding: 0.2em;
      // border-bottom: 1px solid rgba(0, 0, 0, 0.168627451);
      text-align: center;
      // padding: 1em;
    }
    th {
      background-color: #e8e8e8;
      padding: 0.3em 0;
      input {
        background: transparent;
        font-family: system-ui;
        border-radius: 3px;
      }
    }
    th > span {
      margin: 0px 0.5em;
    }
    tbody {
      display: block;
      // max-height: 100%;
      // max-height: 40rem;
      // overflow: auto;
      .highlight {
        color: red;
      }
    }
    thead,
    tbody tr {
      display: table;
      width: 100%;
      table-layout: fixed;
    }
    td:last-child,
    th:last-child {
      width: 5em;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  .footer {
    display: flex;
    column-gap: 1em;
    align-self: self-end;
  }
}

table tbody tr td {
  padding: 0;
  border: 1px solid rgba(0, 0, 0, 0.06);
}
