.notValidUrl {
  width: 100%;
  background: black;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  flex-direction: column;
  #error-text {
    font-size: 1.3em;
    /* font-style: italic; */
    text-transform: uppercase;
    color: transparent;
    -webkit-text-stroke: #efefef;
    -webkit-text-stroke-width: 1px;
    text-shadow: 0px 1px 1px #f4f4ff;
    transition: all 0.5s ease-in-out;
    text-align: center;
    letter-spacing: 1px;
  }
  .errorIcon-container {
    .errorIcon {
      width: 8em;
      height: 7em;
      animation: myfirst 1s linear 1s infinite;
    }

    @keyframes myfirst {
      0% {
        rotate: 0deg;
      }
      25% {
        rotate: 5deg;
      }
      50% {
        rotate: 0deg;
      }
      75% {
        rotate: -5deg;
      }
      100% {
        rotate: 0deg;
      }
    }
  }
}
