table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  tbody {
    tr {
      td {
        padding: 0.5rem;
        border: 1px solid rgba(0, 0, 0, 0.06);
      }

      td:nth-child(2) {
        border-left: 1px solid rgba(0, 0, 0, 0.06);
      }

      &:hover {
        background: rgb(251, 251, 251);
        transition: background 1s cubic-bezier(0.075, 0.82, 0.165, 1);
      }
    }
  }
}
.alarm-info-popup {
  .alarm-info-popup-header {
    color: white;
    background-color: black;
  }
  .eam-container {
    display: flex;
    flex-direction: column;
    row-gap: 0.5em;
    padding: 1em;
    background: #e2e2e2;
    border-radius: 10px;
    .eam-heading {
      font-weight: 600;
    }
    .eam-info-conatiner {
      display: flex;
      flex-direction: column;
      row-gap: 0.5em;
      .eam-info-row {
        display: flex;
        .key {
          min-width: 10rem;
        }
      }
    }
    .eam-workflow-info {
      .eam-workflow-heading {
        color: rgb(0 0 0 / 73%);
        font-weight: 600;
      }
      .eam-workflow-card-conatiner {
        display: flex;
        flex-direction: column;
        row-gap: 0.5em;
        .EamWorkflowCard {
          width: fit-content;
          padding: 0.5em 1em;
          background: white;
          border-radius: 0.5em;
          box-shadow: rgba(0, 0, 0, 0.24) 0px 1px 2px;
          .eam-info-row {
            display: flex;
            .key {
              min-width: 10rem;
            }
          }
        }
      }
    }
  }
}
.alarm-info-table {
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-bottom: 1rem;
}
.alarm-info-row {
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  div:nth-child(1) {
    border-right: 1px solid rgba(0, 0, 0, 0.06);
    min-width: 10rem;
  }

  &:hover {
    background: rgb(251, 251, 251);
    transition: background 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
}
.alarm-filters-container {
  background: #bebebe;
  display: flex;
  flex-wrap: wrap;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border-radius: 0.25rem;
  gap: 0.5rem;
  .filter-input {
    background-color: #d9d9d9 !important;
  }
  > div {
    flex: 1 1 20rem;
  }
}
.file {
  opacity: 0;
  width: 0.1px;
  height: 0.1px;
  position: absolute;
}
.file-input {
  width: 100%;
}
.file-input label {
  cursor: pointer;
  display: block;
  outline: 0;
  display: inline-block;
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1.5;
  text-align: center;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
  color: #0d6efd;
  border-color: #0d6efd;
  &:hover {
    color: #fff;
    background-color: #0d6efd;
    border-color: #0d6efd;
  }
}
.alarms-container {
  // font-family: system-ui !important;
  display: flex;
  flex-direction: column;
  row-gap: 0.5em;
  height: 100%;
  .severity-counts {
    display: flex;
    column-gap: 0.5em;
    align-items: center;
    .count {
      color: white;
      padding: 1em;
      border-radius: 50%;
      width: 2.5em;
      display: flex;
      height: 2.5em;
      font-size: 0.9em;
      justify-content: center;
      align-items: center;
    }
    // font-size: 0.8;
  }
  .alarm-table-container {
    height: 100%;
  }
}

.alarm__info__other-attachments__attachment-name {
  color: #6495ed;
  &:hover {
    cursor: pointer;
    color: #4169e1;
  }
}

.alarm-info-popup-attachment-info-container {
  box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
  // width: max-content;
  // height: max-content;
  // max-width: 40%;
  width: 40%;
  height: max-content;
  max-height: 100%;
  padding: 1em;
  .alarm-info-popup-image {
    display: flex;
    justify-content: center;
    img {
      animation: fadeIn 1s;
    }
  }

  .alarm-images-preview-container {
    .alarm-images-preview-previous-navigation,
    .alarm-images-preview-forward-navigation {
      color: white;
      background: #a5a5a5;
      height: 30px;
      width: 30px;
      margin: 10px;
      font-size: 20px;
      border-radius: 2px;
      border: medium none;
      &:hover {
        background: rgb(117, 117, 117);
      }
    }
    .alarm-images-preview-previous-navigation::before {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      content: "\003c";
    }
    .alarm-images-preview-forward-navigation::before {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      content: "\003e";
    }
  }
  .alarm__info__image-view__image-name {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #6495ed;
    text-decoration: none;
    &:hover {
      cursor: pointer;
      color: #4169e1;
    }
  }
}

.alarm__fmea--container {
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  background-color: var(--background-gray);
  padding: 0.5rem;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.alarm__fmea-view {
  .fmea-list__row {
    display: flex;
    flex-grow: 1;
    border: 1px solid var(--light-gray);
    border-radius: 2px;
    padding: 0.25rem;
    transition: 0.25s all;

    svg {
      transition: 0.25s all;
      &:hover {
        color: red;
      }
    }

    &:hover {
      background-color: var(--gray-100);
    }

    .data--container {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      gap: 0.5rem;

      span {
        font-weight: 600;
      }
    }
  }
}

.alarm__fmea-view--wrapper {
  border: 1px solid rgba(0, 0, 0, 0.06);
  padding: 0.5rem;
  margin-bottom: 1rem;
}

@media screen and (min-width: 768px) {
  .alarm__fmea-view .fmea-list__row .data--container {
    flex-direction: row;

    .mode {
      flex-basis: 100%;
      flex-grow: 1;
    }
    .cause,
    .effect {
      flex-basis: 50%;
      flex-grow: 1;
    }
  }
}

@media screen and (min-width: 1024px) {
  .alarm__fmea-view .fmea-list__row .data--container {
    .mode,
    .cause,
    .effect {
      flex-basis: 0%;
    }
  }
}
