.zoom-component-main-container {
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  width: fit-content;
  position: fixed;
  top: 20%;
  right: 3%;
  //left: 10px;
  z-index: 1200;
  .box-theme {
    background: #69656594;
    color: white;
    width: fit-content;
    height: fit-content;
    padding: 5px;
    border-radius: 10px;
  }
  .percent-container {
    transition: all 0.7s ease-out;
    opacity: 1;
  }
  .fade {
    transition: all 0.6s ease-out;
    opacity: 0;
  }

  .tool-container {
    align-self: flex-end;
    .act-btn-icon {
      padding: 20% 0%;
      .icon {
        width: 2vw;
        height: 3vh;
      }
      .icon:hover {
        color: #5bcd8d;
      }
    }
  }
}
