.dynamicFieldRender {
  .addFieldActionBtn {
    margin-bottom: 1em;
  }
  .fields {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;

    .fieldRow {
      display: grid;
      grid-gap: 10px;
    }
    .assetAffected {
      grid-template-columns: 17fr 17fr 1fr;
    }
    .detainedTrains {
      grid-template-columns: 20fr 10fr 10fr 10fr 1fr;
    }
  }
}
