.telemetry {
  position: static;
  //background-color: #ffffff;
  // box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  // border-radius: 5px;
  // padding: 0.5em;
  display: flex;
  width: 100%;
  height: 100%;
  row-gap: 1em;

  .print-icon {
  }

  .blink_selected_row_color {
    background: #ff0;
    color: #000;
    animation: flash 4s linear 1;
    //transition: background 2000ms linear;
  }
  @keyframes flash {
    0% {
      background-color: #ff0;
    }
    25% {
      background-color: #ffff0036;
    }
    50% {
      background-color: #ff0;
    }
    75% {
      background-color: #ffff0036;
    }
    100% {
      background-color: #ff0;
    }
  }

  .blink_selected_coloumn_color {
    // color: #F00;
    // font-weight: bold;
    //background: #c7c707;
    //border-bottom: 1px solid #ccc;
    //animation: blink 4s linear infinite;
    //color: #F00;
    //font-weight: bold;
  }

  .MuiDataGrid-columnHeaders {
    //min-height: 55px !important;
    //max-height: 55px !important;
    //line-height: 55px !important;

    .MuiDataGrid-columnHeaderTitleContainerContent {
      line-height: 15px;
      font-size: 12px;
    }
  }

  .container-station-selector {
    width: 500px;
    //border: 1px solid black;
    position: absolute;
    left: 882px;
    top: 64px;
  }
  .container-station-rack-selector {
    position: absolute;
    //border: 1px solid black;
    width: 100px;
    height: 50px;
    left: 372px;
    top: 138px;

    .telemetry-device-button {
      background-color: #ffffff;
    }
    .MuiButton-contained {
      background-color: #1976d2;
      color: #fff;
    }
  }

  .telemetry-filter-component {
    display: flex;
    background: #80808014;
    padding: 0.5em 1em;
    border-radius: 5px;
    width: 100%;
    column-gap: 0.5rem;
    .filterinuptList-added-filters {
      flex: 20;
      display: flex;
      flex-direction: column;
      row-gap: 0.5em;
      .filterinuptList {
        display: flex;
        column-gap: 0.4em;
        .filter-input {
          flex: 1;
          min-width: 15em;
        }
      }
    }
    .hidden {
      z-index: -1;
      display: none;
    }
    .hideButton {
      // flex: 1;
      position: relative;
      .dotIcon {
        position: absolute;
        width: 7px;
        top: 5px;
        right: 4px;
        height: 7px;
        background: red;
        border-radius: 100%;
        animation: blink 0.7s step-start 0s infinite;
      }
      @keyframes blink {
        50% {
          opacity: 0;
        }
      }
    }

    // .MuiButton-contained{
    //   background-color: #ff0000 !important;
    // }
    // .MuiButton-outlined{
    //   color: #ff0000 !important;
    //   border-color: #ff0000 !important;
    // }
  }
  .containerWhenFilterHidden {
    display: flex;
    // width: 100%;
    background: unset;
    justify-content: flex-end;
    display: none;
  }
  .hideButton.no-grid-view {
    position: absolute !important;
    top: 75px;
    right: 40px;
    z-index: 1200;
    display: flex;
    flex-direction: row;
  }

  .telemetry-header {
    // //overflow-x: auto;
    // position: relative;
    // min-height: 70px;
    display: flex;
    flex-wrap: wrap;

    // //border: 1px solid black;
    .tab_menu_level_0 {
      width: 100%;
    }
    // .tab_menu_level_0 {
    //   left: 0px;
    // }
    // .tab_menu_level_1.initial_not_expended {
    //   left: 500px;
    //   position: absolute;
    // }
    // .tab_menu_level_1.initial_expended {
    //   left: 0px;
    //   top: 50px;
    // }
    // .tab_menu_level_2.initial_not_expended {
    //   left: 1000px;
    //   position: absolute;
    // }
    // .tab_menu_level_2.initial_expended {
    //   left: 525px;
    //   top: 50px;
    //   position: absolute !important;
    // }

    .highlighted {
      z-index: 1000;
      // background: red;
      position: absolute;
    }
    .highlighted.initial_expended {
      position: unset;
    }

    .blocker-div {
      display: none;
      position: absolute;
      left: 0px;
      top: 0px;
      height: 54px;
      width: 1px;
    }
    .telemetry-block-div {
      display: block;
      background: #e0dcdc;
      z-index: 900;
      width: 100%;
      opacity: 0.8;
    }
    .selected-mui-telemetry-tab-btn {
      // background-color: red;
      color: red !important;
      border-bottom: 3px solid red !important;
    }
  }
  .telemetry-device-buttons.highlighted {
    .telemetry-device-button.MuiButton-outlined {
      // background: #fff;
      // border-bottom: 3px solid red;
      // border-top: none;
      // border-right: none;
      // border-left: none;
      // background-color: #fff;
      border: none;
      color: #6c757d;
      background-color: #ffffff;
      box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
      // box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      font-weight: 600;
    }
  }

  /* height: 100%; */
  flex-direction: column;
  .telemetry-device-buttons {
    margin: 0.5rem 0;
    //position: absolute;
    left: 0px;
    top: 0px;
    z-index: 800;
    .telemetry-device-button {
      margin-left: 10px;
      margin-bottom: 5px;
      white-space: nowrap;
    }
    .btn_disabled{
      background-color: #cacaca !important;
      color: #727272 !important;
    }
    .btn_hide{
      display: none;
    }
  }
  .telemetry-active-device {
    margin: 2px 0;
    .telemetry-active-device-icon {
      color: orange;
    }
    .telemetry-active-device-text {
      vertical-align: text-bottom;
      margin-left: 5px;
    }
  }
  .telemetry-device-ips > :not(style) {
    margin: 1em;
    width: 128px;
    height: 128px;
  }
  .telemetryTableArea-defined {
    height: 89%;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
    padding: 0.5em;
    position: relative;

    .otColor {
      background-color: #f4a6a6;
    }

    .otColor:hover {
      background-color: rgb(255 0 0 / 55%) !important;
    }

    .testModeColor {
      background-color: #ffa50057;
    }
    .testModeColor:hover {
      background-color: #ffa50057 !important;
    }
    .TC-CHART-BTN {
      margin-left: 5px;
      height: 25px;
      border-left: 1px solid;
      border-radius: 0%;
    }
    .TC-CHART-BTN:hover {
      color: #0051a1;
    }
    .custom-toolbar.MuiDataGrid-toolbarContainer {
      padding-right: 60px;
    }

    .hideButton {
      // flex: 1;
      //position: relative;
      position: absolute;
      right: 20px;
      z-index: 1200;
      cursor: pointer;

      .dotIcon {
        position: absolute;
        width: 7px;
        top: 5px;
        right: 4px;
        height: 7px;
        background: red;
        border-radius: 100%;
        animation: blink 0.7s step-start 0s infinite;
      }
      @keyframes blink {
        50% {
          opacity: 0;
        }
      }
    }
    .websocketLiveDataFlash {
      position: absolute;
      left: 0px;
      z-index: 1200;
      cursor: pointer;
      top: 6px;
    }
  }
  .sg-table-area {
    .data-table {
      height: 40vh !important;
      // height: 40vh !important;
      // border: 1px solid rgba(194, 191, 191, 0.070588);
      // background-color: rgba(159, 169, 197, 0.0901960784);
      // box-shadow: rgb(78 102 128 / 65%) 1px 2px 8px 1px;
      .MuiDataGrid-toolbarContainer {
        .MuiButton-root {
          font-size: 0.78rem;
          .css-y6rp3m-MuiButton-startIcon > *:nth-of-type(1) {
            font-size: 14px;
          }
        }
        .MuiInputBase-inputTypeSearch {
          width: 120px;
        }
        .MuiInputBase-inputTypeSearch + .MuiIconButton-root {
          display: none;
        }
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 1.2rem;
        }
      }
    }
  }
}

.MuiDataGrid-virtualScrollerRenderZone {
  width: 100%;

  .MuiDataGrid-row {
    border-bottom: 1px solid #9e8a8a;
    width: 100% !important;
  }
}

.actionCell {
  // background-color: red;
  // flex: 1;
  width: 1em !important;
  min-width: 1em !important;
  // max-width: 1em !important;
  padding: 1em !important;
}

#responsive-dialog-title.point-machine,
.alarm-info-popup-header.flex.point-machine {
  .print-icon {
    position: absolute;
    right: 55px;
    top: 9px;
  }
}
#responsive-dialog-content.point-machine,
.responsive-dialog-content.point-machine {
  .snapshot-title {
    padding: 10px;
    font-size: 12px;
    font-weight: bold;
    text-align: center;
  }
}
.viberation-view {
  .snapshot-title {
    text-align: left !important;
  }
}
.hideVisibility {
  visibility: hidden;
}

.hide-during-print,
.showIt {
  display: block !important;
}
.show-during-print,
.hideIt {
  display: none !important;
}

@media print {
  .MuiDrawer-docked.sidebar-drawer,
  .telemetryTableArea-defined {
    display: none !important;
  }
  .MuiAppBar-root.site-header,
  .zoom-component-main-container-telemetry {
    display: none !important;
  }
  .telemetry-header,
  .tabular-graphical-btn-grp {
    display: none !important;
  }
  .component-page.fullWidth {
    margin-top: 0px !important;
  }

  .hide-during-print,
  .hideButton {
    display: none !important;
  }
  .show-during-print {
    display: block !important;
  }
  #responsive-dialog-title.point-machine,
  .alarm-info-popup-header.flex.point-machine,
  .alarm-info-popup-header {
    display: none !important;
  }
  svg:not(.svg_block svg),
  canvas:not(.viberation-view canvas) {
    width: 99% !important;
    height: 99% !important;
  }
}
