.data-table {
  font-size: 12px;
  height: 100% !important;
  // height: 80vh !important;
  // width: 100% !important;
}
.data-table-fullScreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw !important;
  height: 100vh !important;
  overflow: hidden;
  z-index: 1200;
  background-color: #ffffff;
  // background: linear-gradient(180deg, #e6eff5, #ffffff) !important;
  margin: 0 !important;
  padding: 1em;
}
.dataGridLoading {
  font-size: 1.5em !important;
}
.MuiDataGrid-root {
  border: none !important;
  .custom-toolbar {
    display: flex;
    justify-content: end;
    column-gap: 0.5em;
    border-bottom: 1px solid #0000001c;
    .fullscreen-icon {
      padding: 0 !important;
      color: black;
      min-width: 0 !important;
    }
    .gridToolbarButton {
      font-size: 0;
      min-width: max-content;

      .MuiButton-startIcon {
        padding: 0;
        margin: 0;
        color: black !important;
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 28px !important;
        }
      }
    }
  }
  .MuiDataGrid-main {
    .MuiDataGrid-columnHeaders {
      background-color: grey;
      color: #ffffff;
      .MuiDataGrid-columnSeparator {
        display: none;
      }
    }
    .MuiDataGrid-cell {
      border-bottom: 1px solid rgb(0 0 0 / 32%);
      .MuiDataGrid-cellContent {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}
.css-n3z9fz-MuiPopper-root-MuiDataGrid-panel {
  position: absolute !important;
  // inset: 0px auto auto 0px !important;
  top: 34% !important;
  left: 80% !important;
  transform: none !important;
