@media all {
  .page-break {
    display: none;
  }
}

@media print {
  html, body {
    height: initial !important;
    overflow: initial !important;
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
  }
}

@media print {
  .page-break {
    margin-top: 1rem;
    display: block;
    page-break-before: auto;
  }
}

@page {
  size: auto;
  margin: 20mm;
}

@media print {
  div {
    break-inside: avoid;
    page-break-inside: avoid;
  }
}


body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", OpenSans, sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(43ged,#4158D0,#C850C0,#FFCC70) */
  background: linear-gradient(180deg, #e6eff5, #ffffff) !important;

  /* #FFFFFF */
}

p {
  margin: 0;
}

code {
  font-family: OpenSans, source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.9);
  }
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 1.5em;
  height: 1.5em;
  // margin: 0.5em;
  margin: 5px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  // width: 64px;
  // height: 64px;
  // margin: 8px;
  // height: 1em;
  // width: 1em;
  height: 100%;
  width: 100%;
  // border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  // border-color: #fff transparent transparent transparent;
}

.lds-ring--white div {
  border: 3px solid white;
  border-color: white transparent transparent transparent;
}
.lds-ring--black div {
  border: 3px solid black;
  border-color: black transparent transparent transparent;
}

