.vlc-view-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    border-radius: 8px;
    background: #ffffff;
    position: relative;
    flex-direction: column;

    .print-icon {
        position: absolute;
        right: 1%;
        top: 0px; 
      }

    
    .view-history { 
      text , path, rect, circle {
        cursor: pointer;
      }
      text:hover , path:hover, rect:hover, circle:hover {
        //fill: #1a116b;
        cursor: pointer;
        opacity: 0.7;
      }
    }
    .view-history:hover {
      //fill: #1a116b;
      cursor: pointer;
      opacity: 0.7;
    }

}

