#pss-tooltip {
  height: fit-content;
  width: 14em;
  padding: 0.5em;
  //background: #888;
  background: linear-gradient(#e7e1e1, #faf8f9);
  font-size: 0.8em;
  border-radius: 0.4em;
  z-index: 1200;
  /* font-size: 2vw; */
  // background-color: rgb(55 54 64);
  position: fixed;
  // color: #FEFEFE;
  // color: #ffffff;
  animation: fadeIn 0.4s;
  //display: flex;

  .column {

    .row {
      column-gap: 0.5em;
      width: 100%;
      display: flex;
      border-bottom: 0.1px solid #b5999969;
      /* justify-content: center; */
      //font-family: "Open Sans";
      font-weight: 600;
      //font-feature-settings: "pnum";
      //font-variant-numeric: proportional-nums;
      padding: 5px 0px 3px 0px;  

      .asset-name-link {
        display: flex;
  
        .asset-name {
          padding: 2px 3px 0px 0px;
        }
      }
      .asset-name-link.with-alarm {
        color: #f00;      
      }

     
    }
    .row.heading {
      padding: 5px 5px 5px 12px;
      text-align: center;
    }
  
  }

  .column.dynamic {
    position: absolute;
    left: 177px;
    //background: #fff;
    width: 148px;
    padding: 10px;
    border-radius: 10px;
    top: -14px;
    background: #f0eeee;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
      //   background-color: rgba(0, 0, 0, 0);
    }
    30% {
      opacity: 0.4;
      // background-color: rgb(102, 104, 105);
    }
    100% {
      opacity: 1;
    }
  }
}

#yard-tooltip.with-alarm {
  background: #f3afaf;

}
#yard-tooltip.no-alarm {
  background: #ffffff;
  
}
#yard-tooltip.pss {
  background: #dce0e7;
  
}