.history-mock-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  .historyTableContainer {
    height: 100%;
    /* height: 89%; */
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
    padding: 0.5em;
    position: relative;
  }

  .MuiDataGrid-columnHeaders {
    .MuiDataGrid-columnHeaderTitleContainerContent {
      line-height: 15px;
      font-size: 12px;
    }
  }

  
}

.parent-chart-view-history {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 0.3em;
  // position: relative;
  .historyChartViewHeader {
    display: flex;
    width: 100%;
    justify-content: space-between;
    position: relative;

    .snapshot-title {
      padding: 10px;
      font-size: 12px;
      font-weight: bold;
      text-align: center;
    }
    // .print-icon {
    //   position: absolute;
    //   right: 1%;
    //   top: 0px;
    // }
  }
  .historyChartViewComponentContainer {
    height: 100%;
  }


}
