.yard-layout-local-supply-status {
  display: flex;
  border: 2px solid #fff;
  position: absolute;
  padding: 1px;
  flex-direction: column;
  color: #d9d3d3;
  font-size: 13px;
  left: 88%;
  top: 1px;

  .system-status-group-1 {
    display: flex;
    padding: 3px;
    justify-content: center;

    .system-status-group {
      display: flex;
      flex-direction: column;
      align-items: center;

      .system-status-title {
        padding: 3px;
      }
      .system-status-indication {
        width: 15px;
        height: 15px;
        border-radius: 8px;
      }
      .system-status-indication.on {
        background: #8eea8e;
      }
      .system-status-indication.off {
        background: #ea8e8e;
      }
      .system-status-indication.grayedout {
        background: #9e9c9c;
      }
    }
  }
}
