.alarm-circuit-view-popup {
  section {
    display: flex;
    flex-direction: row;

    .circuit-svg-conatiner {
      width: 100%;
      height: 100%;
      svg {
        zoom: 1.5;
        color: #c0dbf1;
        width: 100%;
        height: 100%;
      }
    }
    .no-circuit {
      margin: auto;
      font-weight: 600;
    }
  }

  .print-icon {
    position: absolute;
    right: 55px;
    top: 9px;
  } 
  .responsive-dialog-content-pss {
    display: flex;
    flex-direction: column;
    .snapshot-title {
      padding: 10px;
      font-size: 12px;
      font-weight: bold;
      text-align: left;
    }
  }

  .tableForPrint {
    border: 1px solid #555;
    width: 300px;
    font-family: OpenSans;

    tbody {

      tr {

        td {
          border: 1px solid #555;
          padding: 1px;
          font-size: 9px;
          background: #FFF;
        }
        td.column1 {
          width: 150px;
          font-weight: bold;
        }
        td.column2 {
          width: 100px;
        }
        td.column3 {
          width: 100px;
        }

        td.selected {
          background: #FF0;
        }
    
      }

      tr:first-child td {
          background: #ccc;
          font-weight: bold;
          font-size: 10px;
      }
    }
  }

      
  .view-history { 
    text , path, rect, circle {
      cursor: pointer;
    }
    text:hover , path:hover, rect:hover, circle:hover {
      //fill: #1a116b;
      cursor: pointer;
      opacity: 0.7;
    }
  }
  .view-history:hover {
    //fill: #1a116b;
    cursor: pointer;
    opacity: 0.7;
  }

}
