.dashboardCard {
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  padding: 0.4em;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  /* width */
  ::-webkit-scrollbar {
    width: 5px;
  }
  .header {
    display: flex;
    justify-content: space-between;
    padding: 0.2em;
    align-items: center;
    flex: 0.5;
    width: 100%;
    .heading {
      font-weight: bold;
    }
  }
  .chart-table-container {
    flex: 9;
  }
  .dashboardTable {
    .data-table {
      height: 100% !important;
    }
    .data-table {
      .MuiDataGrid-toolbarContainer {
        .MuiButton-root {
          font-size: 0.7125rem;
          .css-y6rp3m-MuiButton-startIcon > *:nth-of-type(1) {
            font-size: 14px;
          }
        }
        .MuiInputBase-inputTypeSearch {
          width: 120px;
        }
        .MuiInputBase-inputTypeSearch + .MuiIconButton-root {
          display: none;
        }
        .css-i4bv87-MuiSvgIcon-root {
          font-size: 1.2rem;
        }
      }
    }
  }
}
.type-status-count-container {
  grid-area: type-status-count-container;
}
.station-count-container {
  grid-area: station-count-container;
  min-width: 31em;
  .dashboardChart {
    display: flex;
    column-gap: 0.8em;
    justify-content: center;
    align-items: center;
    .chart-conatainer {
      flex: 1;
      display: flex;
      flex-direction: column;
      .chart-heading {
        align-self: center;
        font-size: 1em;
        font-weight: bold;
      }
      .chart {
        .CircularProgressbar-text {
          text-anchor: middle;
          alignment-baseline: middle;
        }
      }
    }
  }
}
.alarm-category-failure-count-container {
  grid-area: alarm-category-failure-count-container;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  .header {
    width: 100%;
    .heading {
      font-size: 1em;
      font-weight: bold;
    }
  }
  .dashboardChart {
    width: 53%;
  }
}
.station-wise-count {
  grid-area: station-wise-count;
}
.dashboard-maintenance-roaster {
  grid-area: dashboard-maintenance-roaster;
}
.periodic-maintenance-dashboard {
  grid-area: periodic-maintenance-dashboard;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  row-gap: 0.2em;
  height: 100%;
  .dashboardChart {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    .chart-container {
      width: 90%;
      //   height: 40%;
      //   padding: 0.3em 0em;
    }
    .card-container {
      width: 100%;
      .dash-count-card {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.2em;
        border-bottom: 1px solid #0000002b;

        .count-type {
          flex: 2;
          font-weight: bold;
          display: flex;
          align-items: center;
          .colour-box {
            width: 10px;
            height: 10px;
            border-radius: 3px;
            margin: 5px;
            border: 1px solid rgba(247, 1, 1, 0.2);
          }
        }
        .count {
          flex: 1;
        }
        .act-icon {
          flex: 0.4;
          // color: #a59e9e;
          // background-color: red;
          border-radius: 0.5em;
        }
      }
      .dash-count-card:last-child {
        border: none;
      }
      //        .act-icon{
      //   border-radius: 0.5em;
      //   background-color: red;
      // //   width: 50%;
      //        }
    }
  }
}
