.component-container{
    display: flex;
    flex-direction: column;
    row-gap: 1em;
    height: 100%;
    .form-component-container{
        flex: 2;
        border: 1px solid rgb(0 0 0 / 20%);
        min-width: 15em;
        width: 35%;
        display: flex;
        flex-direction: column;
        padding: 1em;
        row-gap: 1em;
        background-color: #ffffff;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
        border-radius: 10px;
        overflow: auto;
        cursor: grab;
        .typeHeading{
            text-align: center;
            position: sticky;
            top: -16px;
            font-weight: 600;
            z-index: 1;
            background: #ffffff;
            border-bottom: 1px solid #5a5d60;;;
        }
        .rule-line{
            margin: 0;
            height: 2px;
            background: #6d6f9f;
            color: #a1c7ff;
        }
        .row{
            display: flex;
            width: 100%;
            column-gap: 1em;
            align-items: center;
            box-shadow: 0 1px 1px 0px rgb(0 0 0 / 20%);
            border-radius: 6px;
            padding: 0.5em;
            border: 1px solid #e6e6e6;
            padding: 0.5em;
            background-color: #fff;
            color: #5f6164;
            font-weight: 600;
            .icon{
                align-items: center;
                display: flex;
                padding: 5px;
                justify-content: center;
            }
        }
        .dragging{
            opacity: 0.5;
            background-color: lightblue;
        }
    }
    
}
