.maintenace-dashBoard-container {
  padding: 0.5em;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  grid-area: maintenance-dashboard-chart;
  .card-heading {
    font-size: 1.2em;
    font-weight: bold;
  }
  .chart-container {
    width: 60%;
    // height: 40%;
    padding: 0.3em 0em;
  }
  .card-container {
    width: 100%;
    .dash-count-card {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0.2em;
      border-bottom: 1px solid #0000002b;

      .count-type {
        flex: 2;
        font-weight: bold;
        display: flex;
        align-items: center;
        .colour-box {
          width: 10px;
          height: 10px;
          border-radius: 3px;
          margin: 5px;
          border: 1px solid rgba(247, 1, 1, 0.2);
        }
      }
      .count {
        flex: 1;
      }
      .act-icon {
        flex: 0.4;
        // color: #a59e9e;
        // background-color: red;
        border-radius: 0.5em;
      }
    }
    .dash-count-card:last-child {
      border: none;
    }
    //        .act-icon{
    //   border-radius: 0.5em;
    //   background-color: red;
    // //   width: 50%;
    //        }
  }
}
.maintenance-information-popup {
  min-height: 20em !important;
  display: flex;
  flex-direction: column;
  padding: 0.4em 1em !important;
  row-gap: 0.8em;
  .header {
    // display: flex;
    // column-gap: 1em;
    // .filter-input{
    //   flex: 7;
    // }
    // .pagination{
    //   flex: 3;
    //   max-width: 1em;
    // }
  }
  .cards {
    display: flex;
    flex-direction: column;
    row-gap: 1em;
    .asset-info-card {
      width: 100%;
      padding: 0.6em;
      border-radius: 0.4em;
      display: flex;
      flex-direction: column;
      row-gap: 0.2em;
      box-shadow: rgb(60 64 67 / 30%) 0px 1px 2px 0px, rgb(60 64 67 / 15%) 0px 2px 6px 2px;
      .head {
        font-weight: bold;
        // color: hotpink;
      }
      .middle {
        .middle-content {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          .key {
            font-size: 1em;
            color: grey;
          }
          .value {
            font-size: 1.1em;
            font-family: fangsong;
            color: rgb(0, 0, 0);
            font-weight: bold;
          }
        }
      }
      .footer {
        .remarks {
          font-style: oblique;
        }
      }
      .card-sec {
        display: flex;
        justify-content: space-between;
      }
    }
  }
}
