.datalog-iom-container {
  display: flex;
  overflow: auto;
  height: 100%;
  width: 100%;
  display: flex;
  padding: 1em;
  flex-direction: column;
  .iom_header {
    display: flex;
    align-items: center;
    left: 0;
    position: sticky;
    gap: 0.5rem;
    top: 0;
  }
  .iomSvgContainer {
    overflow: auto;
    height: 100%;
    display: flex;

    width: 100%;
    // min-width: 111% !important;
    column-gap: 1em;
    .iomSvg {
      // flex: 1;
      width: 100%;
      height: 95%;
    }
    .hideSwitchSection {
      #L2-Switch-Group {
        display: none;
      }
    }
    .hideIomSection {
      #SINGLE-RACK-IOM {
        display: none;
      }
    }
  }
}
