.alarm-notifications-popper {
  background-color: white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
}

.alarm-notifications-popper__notifications-list--container {
  padding: 0.5rem;

  .popper--heading {
    border-bottom: 1px solid #eaecef;
    .container--title {
      font-size: 1.1rem;
      font-weight: 600;
      margin: 0;
    }
    .switch-label {
      font-size: 0.9rem !important;
    }
  }

  .items--container {
    max-height: 30rem;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;

    .item-row {
      background-color: var(--gray-50);
      transition: 0.3s all;
      border-bottom: 1px solid var(--gray-200);

      &:hover {
        background-color: var(--gray-200);
        .unread-icon {
          background-color: var(--blue-600);
        }
      }
      .unread-icon {
        border-radius: 100%;
        width: 0.7rem;
        display: inline-block;
        height: 0.7rem;
        background-color: var(--blue-500);
        cursor: pointer;
        transition: 0.25s all;
      }
    }
  }
}

.alarm-notification__row--title {
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.alarm-notification__row--date {
  flex-grow: 1;
  color: var(--gray-500);
}

.alarm-notification__row--description {
  color: var(--gray-700);
  display: flex;
  align-items: flex-start;

  svg {
    font-size: 1.2rem;
    color: var(--gray-400);
  }
}
