.tpws-other-view-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 8px;
  background: #ffffff;
  flex-direction: column;
  position: relative;
  overflow: auto;

  .snapshot-title {
    padding: 10px;
    font-size: 12px;
    font-weight: bold;
  }
  .print-icon {
    position: absolute;
    right: 1%;
    top: 0px;
  }
}
