.pss-view-container {
  height: 100%;
  display: flex;
  column-gap: 1em;
  padding: 0.5em;
  .pss-rack {
    display: flex;
    flex-direction: column;
    row-gap: 1em;
    align-items: center;
    padding: 1em 0.4em;
    background-color: #e2e2e2;
    border-radius: 1em;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    .rackHeader {
      background: #0d99ff;
      color: #ffffff;
      padding: 0.5em 1em;
      border-radius: 0.5em;
    }
    .rackCardContainer {
      width: 100%;
      display: flex;
      height: 90%;
      row-gap: 0.5em;
      flex-direction: column;
      flex-wrap: wrap;
      padding: 0em 0.5em;
      min-width: 35em; //   align-items: center;
      .pssCard {
        width: 30%;
        border: 1px solid;
        height: 4em;
        // background-color: #68a952;
        border-radius: 0.3em;
        font-size: 1.1em;
        padding: auto 0em;
        min-width: 10em;
        text-align: center;
        // display: flex;
        // justify-content: center;
        // align-items: center;
        cursor: pointer;
      }
    }
  }
  .UPS1 {
    flex: 10;
  }
  .UPS2 {
    flex: 10;
  }
  .ELD {
    flex: 8;
  }
  .pssTooltipConatiner {
    position: fixed;
    background: #b49898;
    padding: 0.5em;
    display: flex;
    flex-direction: column;
    border-radius: 0.3em;
    row-gap: 0.5em;
    font-weight: 600;
    .row {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .key {
        min-width: 4em;
      }
      .val {
        max-width: 8em;
        text-align: end;
      }
    }
  }
}
