.staton-alh-component-main-container {
  max-width: 90%; 
  margin: auto;
  //border: 2px solid black;
  padding: 10px;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  height: 80px;
  background: #fff;
  //box-shadow: 0px 0px 0px 2px #b2b2a8;

  .MuiStepper-horizontal {
    position: absolute;
    left: 0px;
    top: 10px;
    transition: left 0.5s;

    .MuiStep-horizontal {
      width: 60px;

      .MuiStepConnector-horizontal {
        left: calc(-50%);
        right: calc(50%);

        .MuiStepConnector-lineHorizontal {
          border-color: #1a551f;
          border-top-width: 3px;
        }
      }
      
      .MuiStepLabel-alternativeLabel {
        position: relative; 
      }
      .MuiStepButton-horizontal.ALH {
        .MuiStepLabel-iconContainer {
          border-radius: 2px;
          background-color: #413e3e;
          height: 4px;
          width: 4px;
          margin: 10px 0px 0px 0px;
          z-index: 10;
        }

        .MuiStepLabel-labelContainer {
          .MuiStepLabel-label { 
            .label-vertical.ALH {
              position: absolute;
              left: calc(50% - 1px);
              top: calc(-100%);
              border: 1px solid #1a551f;
              height: 15px; 
      
              .label-vertical-line { 
                display: block;
                //border-top-style: solid;
                //border-color: #1a551f;
                position: absolute;
                left: -12px;
                top: 17px;
                font-size: 10px;
                color: #eee5e5; 
                border: none; 
              }
            }
    
            .ALH_icon {
              width: 25px;
              height: 15px;
              border: 1px solid black;
              border-radius: 0px 0px 12px 0px;

              .station-alh-label {
                margin: 16px 0px 0px 0px;
                display: inline-block;
                font-size: 11px;
                color: #000;
              }
            }
            
          }
          .MuiStepLabel-label.Mui-active {
            .ALH_icon {
              background: #4852dd;
            }
          }
        }

        
        
      }
      .MuiStepButton-horizontal.STATION {
        .MuiStepLabel-iconContainer {
          display: none;
        }
        .MuiStepLabel-labelContainer {
          .MuiStepLabel-alternativeLabel {
            border: 1px solid black;
            padding: 3px 10px 3px 10px;
            margin: -2px 0px 0px 0px;
            background: #ccc;
            z-index: 10;

            .label-vertical {
              display: none;           
            }
          }
          .MuiStepLabel-alternativeLabel.Mui-active {
            background: #4852dd;
            color: #ece5e5;
          }          
        }        
      }

      .MuiStepLabel-iconContainer.MuiStepLabel-alternativeLabel {
        border-radius: 13px;
        background-color: #b4a9a9;
        height: 26px;
        width: 26px;
      }
      .MuiStepIcon-root {
        visibility: hidden;
      }
      .MuiStepIcon-root.Mui-active {
        .MuiStepIcon-text  {
          font-size: 0px;          
        }
      }
  
    }

  } 

  .station_middle_menu {
    position: absolute;
    //left: 198px;
    top: -10px;
  }
}

