.dashboard-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas:
    "station-count-container type-status-count-container alarm-category-failure-count-container"
    "station-wise-count dashboard-maintenance-roaster periodic-maintenance-dashboard";
  grid-gap: 10px;
  grid-template-rows: 1fr 2fr;
  grid-template-columns: 1.5fr 2fr 1fr;
  position: relative;
}
