.form-section-container{
    padding: 1em;
    row-gap: 1em;
    max-height: fit-content;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border-radius: 10px;
    flex: 8;
    background: #eeeeee96;
    border: 1px solid #0000003d;
    .header{
        
        .heading{
        font-size: 1.2em;
        font-weight: 600;
        }
        .sub-heading{
            font-size: 1em;
        }
    }
 
}
.hideSection{
    opacity: 0.6;
}

