.asset-assign-main-container{
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
    padding: 0.5em;
    width: 100%;
    height: 100%;
    row-gap: 0.6em;
    .header{
        align-self: flex-end;
    }
}