// data-card for IPS in Telemetry
.data-card {
  min-width: 230px;
  max-height: 120px;
  border: 1px solid #aaa;
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  .data-card-content {
    padding: 0 32px;
    .data-card-content-text-primary {
      font-size: 27px;
      font-weight: 300;
    }
    .data-card-content-text-secondary {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.6);
      font-weight: 100;
    }
  }
}
.data-card > div {
  padding-bottom: 0 !important;
}
.MuiCardContent-root.data-card:last-child {
  padding-bottom: 0;
}

.basicCard {
  box-sizing: border-box;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  // background-color: rgb(25, 118, 210);
  // color: #ffffff;
  // width: 10em;
  margin-top: -3px;

  width: max-content;
  // height: 2em;
  // height: max-content;
  // box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px 1px;
  // padding: 0 1em;
  padding: 5px 10px;
