.main-container-tc-charts {
  width: 99%;
  //   height: 100%;
  height: fit-content;
  //   background-color: #F0F2F5;
  // display: flex;

  padding: 0 0 !important;

  //   margin: 0 0 !important;
  .header {
    height: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .heading {
      padding: 0;
      margin: 0;
      font-family: math;
    }

    .filters {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .chart-container {
    height: 80%;
    display: flex;
    flex-direction: column;

    // row-gap: 1em;
    // .upper {
    //   flex: 1.5;
    // }
    // .lower {
    //   flex: 1;
    // }
    .part {
      display: flex;
      column-gap: 1.5em;
      justify-content: center;
      align-items: center;
      padding: 1em;

      .chart-box {
        width: 50%;
        // height: 35em;
        border: 1px solid rgba(194, 191, 191, 0.0705882353);
        /* background-color: rgb(190 207 252 / 21%); */
        /* box-shadow: rgb(78 102 128 / 87%) 0px 0px 8px 1px; */
        // box-shadow: 0 0 20px 8px #d0d0d0;
        // box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
        filter: drop-shadow(2px 4px 6px rgb(0 0 0 / 25%));
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: #f2f2f2;

        .card-header {
          display: flex;
          justify-content: space-between;
          padding: 4px;
          align-items: center;
          height: 10%;
          width: 100%;

          .card-heading {
            font-weight: 600;
            text-shadow: 1px 7px 23px #0000004d;
            font-size: 20px;
          }

          .fullSCreenIcon {
            align-self: right;
            // cursor: pointer;
          }
        }

        .chart {
          // height: 90% !important;
          width: 69% !important;
          display: flex;
          justify-content: center;
          align-items: center;

          .chart-canvas {
            // width: 80% !important;
            // height: 100% !important;
          }
        }
      }
      .fullScreen {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10000;
        .chart {
          width: 45% !important;
        }
      }

      // justify-content:;
    }
  }
}
