.rack-view-conatiner {
  display: flex;
  column-gap: 1em;
  flex-wrap: wrap;
  row-gap: 1em;
  justify-content: flex-start;
  overflow: auto;
  padding: 0.5em;

  .rack-view-column {
    padding: 10px 10px 10px 10px;
    background: #CCC;
    margin: 0px 10px 10px 10px;
    min-width: 300px;

    .rack-view-heading {
       text-align: center;
    }
    .rack-view-label-box {
      text-align: center;
      padding: 10px;
      margin: 10px 0px 10px 0px;
      border-radius: 5px;
    }
    .rack-view-label-box.success{
      background: #8fc18f;
    }
    .rack-view-label-box.success:hover {
      background: #8ec28e;
    }
    .rack-view-label-box.failure {
      background: #eb5f4e;
    }
    .rack-view-label-box.failure:hover {
      background: #ec6b5d;
    }
  }

  .rack-view-column:hover {
    background: #d3cbcb;
  }

  .rack-nested-view-column {
    padding: 10px 10px 10px 10px;
    background: #CCC;
    margin: 0px 10px 10px 10px;
    min-width: 300px;


    .rack-view-heading {
      text-align: center;
    }

    .rack-nested-view-container {
      border: 1px solid black;
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      padding: 5px;
     
      .rack-view-label-box {
        text-align: center;
        padding: 10px;
        border-radius: 5px;
      }
  
      .rack-view-inner-container {
        display: flex;
        margin: 0px 5px 9px 5px;

        .rack-view-label-box {
          text-align: center;
          padding: 10px;
          margin: 10px 0px 10px 0px;
          border-radius: 5px;
          border: 1px solid #70a486;
          max-width: 40px;
          height: 150px;
          overflow: hidden;
          display: flex;
          align-items: flex-end;
          
          div {
            min-width: 80px;
            transform: rotate(-90deg);
            transform-origin: left top;
            white-space: nowrap
          }
        }
        .rack-view-label-box.success{
          background: #8fc18f;
        }
        .rack-view-label-box.success:hover {
          background: #8ec28e;
        }
        .rack-view-label-box.failure {
          background: #eb5f4e;
        }
        .rack-view-label-box.failure:hover {
          background: #ec6b5d;
        }
      }
    }
    
  }

  .rack-nested-view-column:hover {
    background: #d3cbcb;
  }
}
