.gis-popup-container {
  display: flex;
  min-width: fit-content;
  height: fit-content;
  row-gap: 0.5em;
  flex-direction: column;
  padding: 0.2em 0.5em;
  //   align-items: center;
  justify-content: center;
  .infoRow {
    display: flex;
    justify-content: space-between;
    column-gap: 1em;
    border-bottom: 0.5px solid #a6a6a6;
    .key {
      color: #3a5d83;
      font-weight: 600;
    }
    .value {
      font-weight: 600;
    }
  }
  .infoRow:last-child {
    border: none;
  }
}
