.toast__progress-bar--container {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 5px;
  background: #f1f1f1;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.toast__progress-bar {
  position: absolute;
  left: 0;
  width: 0;
  height: 100%;
  border-radius: 4px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  animation: progress 6s normal;
}

.hide-during-print {
  display: none !important;
}

@keyframes progress {
  0% {
    width: 100%;
    background: #a28089;
  }
  100% {
    width: 0%;
    background: #d0bdf4;
  }
}
