.popup-container {
  // padding: 0.3em 1em;
  padding: 0.3em 1em !important;
  display: flex;
  flex-direction: column;
  row-gap: 0.6em;
  width: 30em;
  // height: fit-content;
  .displayTab {
    display: flex;
    flex-direction: column;
    // // justify-content: center;
    // align-items: center;
    row-gap: 0.5em;
    .dropdownMarkup {
      display: flex;
      // justify-content: center;
      // align-items: center;
      // flex-direction: column;
      padding: 0.5em;
      min-height: 8em;
      border-bottom: 0.5px solid #bebdbd;
      border-top: 0.5px solid #bebdbd;
      width: 100%;
      .headingPart {
        flex: 1;
        .dd-heading {
          margin: 0;
          // font-size: 1em;
        }
      }
      .optionActPart {
        flex: 3;
        display: flex;
        flex-direction: column;
        row-gap: 0.5em;
        .option-input {
          display: flex;
          align-items: center;
          justify-content: center;
          column-gap: 0.5em;
          .addBtn {
            background: #1976d2;
            color: white;
            border-radius: 7px;
            padding: 0.5em;
          }
        }

        .hor-line {
          width: 100%;
        }

        .option-preview {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          row-gap: 0.2em;
          column-gap: 0.1em;
          overflow-y: auto;
          height: 5em;
          .option-chip {
            font-size: 1em;
            font-weight: bold;
            .chip-icon {
              color: black;
            }
            .chip-icon:hover {
              color: red;
            }
          }
        }
      }
    }
  }
  .analyticsTab {
    display: flex;
    width: 100%;
    column-gap: 1em;
    .severity-tabs {
      flex: 1;
    }
 
    .show{
      display: static;
    }
    .hide{
      display: none;
    }
  }
.valuesRangeTab{
  display: flex;
  flex-direction: column;
  row-gap: 1.5em;
  .validation-row{
    display: flex;
    row-gap: 0.5em;
    flex-direction: column;
    .fields{
      display: flex;
      column-gap: 0.3em;
      .fieldItem{
      flex: 1;
      }
    }
  }
}
  .show {
    display: static;
    transition: ease-out 2s;
  }
  .hide {
    display: none;
  }
