.extraWidth {
  // width: 50em;
  .formContainer {
    flex-direction: row;
    width: 100%;
    column-gap: 1em;

    .ddLeft {
      display: flex;
      // align-items: center;
      // justify-content: center;
      row-gap: 0.5em;
      flex-direction: column;
      flex: 1;
    }
    .ddRight {
      display: flex;
      align-items: center;
      // justify-content: center;
      row-gap: 0.5em;
      flex-direction: column;
      flex: 2;
      .dd-heading {
        margin: 0;
      }
      .option-input {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 0.5em;
        .addBtn {
          background: #1976d2;
          color: white;
          border-radius: 7px;
          padding: 0.5em;
        }
      }
      .option-preview {
        display: flex;
        flex-wrap: wrap;
        row-gap: 0.5em;
        column-gap: 0.4em;
        .option-chip {
          font-size: 1em;
          font-weight: bold;
          .chip-icon {
            color: black;
          }
          .chip-icon:hover {
            color: red;
          }
        }
      }
    }
  }
