.sfr-information-popup {
  box-shadow: rgba(78, 102, 128, 0.56) 0px 0px 3px 1px;
  overflow-y: auto;
  height: 100%;
  .popHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .sfr-popup-content {
    display: flex;
    flex-direction: column;
    row-gap: 0.2em;
    width: 100%;
    height: 100%;
    padding: 0;
    justify-content: center;
    .heading {
      flex: 1;
      font-weight: 600;
      display: none;
    }
    .objDataContainer {
      flex: 8;
      //   width: 90%;
      overflow: auto;
      //   display: flex;
      //   justify-content: center;
      background-color: #e6f9ff;

      .object-data-viewer-list-item {
        margin-bottom: 0.5em;
        border-bottom: 0.2px solid #dadada;
        margin-right: 3em;
        .object-data-viewer-list-item-key {
          font-weight: 600;
          //   border-radius: 1em;
          color: #3a5d83;
          flex: 1;
        }
        .object-data-viewer-list-item-separator {
          margin-right: 10em;
          margin-left: 1em;
        }

        .object-data-viewer-list-item-value {
          flex: 2;
          a {
            color: #1976d2;
          }
        }
      }
    }
    // .detainedTrain {
    //   //   background-color: gray;
    // }
  }
}
