.legends-container {
    display: flex;
    width: 100%;
    justify-content: space-between;
    column-gap: 0.3em;
    .legends {
      display: flex;
      max-width: 24em;
      column-gap: 0.4em;
      font-size: 0.8em;
      overflow: hidden;
      .legend {
        display: flex;
        justify-content: center;
        align-items: center;
        .colour-box {
          min-width: 10px;
          min-height: 10px;
          border-radius: 3px;
          margin: 5px;
        }
       
      }
      .selected{
        text-decoration: line-through;
      }
    }
  }